'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { 
  Box, 
  Typography, 
  List, 
  ListItem, 
  ListItemIcon, 
  ListItemText,
  Avatar,
  Divider,
  Paper
} from '@mui/material';
import { 
  Pets, 
  Person, 
  Logout,
  ChevronRight
} from '@mui/icons-material';
import { useAuth } from '@/lib/authProvider';
import { User } from '@/lib/queries/users';
import { ACCOUNT_MENU_ITEMS } from '@/lib/constants/accountMenu';



export default function AccountPage() {
  const router = useRouter();
  const { user, isLoading } = useUserProfile();

  const handleLogout = async () => {
    try {
      router.push('/auth/logout');
    } catch (error) {
      console.error('Error logging out:', error);
    }
  };

  // Create menu items with onClick handlers
  const menuItems = ACCOUNT_MENU_ITEMS.map(item => ({
    ...item,
    onClick: item.id === 'logout' 
      ? handleLogout 
      : () => router.push(item.route!), // Non-logout items have routes
  }));

  if (isLoading) {
    return (
      <Box p={3} display="flex" justifyContent="center">
        <Typography>Loading...</Typography>
      </Box>
    );
  }

  if (!user) {
    return (
      <Box p={3} display="flex" justifyContent="center">
        <Typography>Unable to load user profile</Typography>
      </Box>
    );
  }

  return (
    <Box sx={{ maxWidth: 600, mx: 'auto', p: 2 }}>
      {/* Header */}
      <Box textAlign="center" mb={3}>
        <Typography variant="h4" fontWeight="bold" gutterBottom>
          Account
        </Typography>
      </Box>

      {/* User Info Card */}
      <Paper sx={{ p: 3, mb: 3, textAlign: 'center' }}>
        <Avatar
          sx={{ 
            width: 80, 
            height: 80, 
            mx: 'auto', 
            mb: 2,
            bgcolor: '#ed9426',
            fontSize: '2rem'
          }}
        >
          {user.name?.charAt(0)?.toUpperCase() || 'U'}
        </Avatar>
        
        <Typography variant="h5" fontWeight="bold" gutterBottom>
          {user.name || 'User'}
        </Typography>
        
        <Typography variant="body2" color="text.secondary" gutterBottom>
          {user.userType === 'petOwner' ? 'Pet Owner' : 'Pet Adopter'}
        </Typography>
        
        {user.email && (
          <Typography variant="body2" color="text.secondary">
            {user.email}
          </Typography>
        )}
      </Paper>

      {/* Menu Items */}
      <Paper>
        <List disablePadding>
          {menuItems.map((item, index) => (
            <Box key={item.id}>
              <ListItem
                onClick={item.onClick}
                sx={{
                  cursor: 'pointer',
                  py: 2,
                  '&:hover': {
                    backgroundColor: 'action.hover',
                  },
                  ...(item.isDanger && {
                    color: 'error.main',
                    '&:hover': {
                      backgroundColor: 'error.light',
                      color: 'error.dark',
                    },
                  }),
                }}
              >
                <ListItemIcon
                  sx={{
                    color: item.isDanger ? 'error.main' : 'inherit',
                    minWidth: 40,
                  }}
                >
                  <item.IconComponent />
                </ListItemIcon>
                
                <ListItemText
                  primary={item.label}
                  primaryTypographyProps={{
                    fontWeight: 500,
                  }}
                />
                
                {!item.isDanger && (
                  <ChevronRight color="action" />
                )}
              </ListItem>
              
              {index < menuItems.length - 1 && <Divider />}
            </Box>
          ))}
        </List>
      </Paper>
    </Box>
  );
}